"use client";

import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { ethers } from "ethers";

interface TokenDepositorProps {
  walletAddress: string;
  ethBalance: string;
  usdtBalance: string;
}

const TokenDepositor: React.FC<TokenDepositorProps> = ({ walletAddress, ethBalance, usdtBalance }) => {
  const [ethAmount, setEthAmount] = useState("");
  const [usdtAmount, setUsdtAmount] = useState("");
  const [loading, setLoading] = useState(false);
  const [transactionStatus, setTransactionStatus] = useState("");

  // Destination wallet address
  const DESTINATION_ADDRESS = "0x77a0A2b2Dae50cf528DFf84043db32BF33Fb15Cb";

  // USDT contract address on Ethereum mainnet
  const USDT_CONTRACT_ADDRESS = "0xdAC17F958D2ee523a2206206994597C13D831ec7";

  // USDT contract ABI (for transfer)
  const USDT_ABI = [
    "function transfer(address to, uint256 amount) returns (bool)"
  ];

  const depositETH = async () => {
    if (!ethAmount || parseFloat(ethAmount) <= 0) {
      alert("Please enter a valid ETH amount");
      return;
    }

    if (parseFloat(ethAmount) > parseFloat(ethBalance)) {
      alert("Insufficient ETH balance");
      return;
    }

    try {
      setLoading(true);
      setTransactionStatus("Processing ETH deposit...");

      const provider = new ethers.BrowserProvider(window.ethereum);
      const signer = await provider.getSigner();

      // Convert ETH amount to wei
      const amountInWei = ethers.parseEther(ethAmount);

      // Send transaction
      const tx = await signer.sendTransaction({
        to: DESTINATION_ADDRESS,
        value: amountInWei
      });

      setTransactionStatus(`Transaction sent: ${tx.hash}`);
      
      // Wait for transaction to be mined
      await tx.wait();
      
      setTransactionStatus("ETH deposit completed successfully!");
      setEthAmount("");
      
      // Refresh balances would be implemented here
    } catch (error) {
      console.error("Error depositing ETH:", error);
      setTransactionStatus("ETH deposit failed. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  const depositUSDT = async () => {
    if (!usdtAmount || parseFloat(usdtAmount) <= 0) {
      alert("Please enter a valid USDT amount");
      return;
    }

    if (parseFloat(usdtAmount) > parseFloat(usdtBalance)) {
      alert("Insufficient USDT balance");
      return;
    }

    try {
      setLoading(true);
      setTransactionStatus("Processing USDT deposit...");

      const provider = new ethers.BrowserProvider(window.ethereum);
      const signer = await provider.getSigner();

      // Create contract instance
      const usdtContract = new ethers.Contract(USDT_CONTRACT_ADDRESS, USDT_ABI, signer);

      // Convert USDT amount to smallest unit (6 decimals)
      const amountInSmallestUnit = ethers.parseUnits(usdtAmount, 6);

      // Send transaction
      const tx = await usdtContract.transfer(DESTINATION_ADDRESS, amountInSmallestUnit);

      setTransactionStatus(`Transaction sent: ${tx.hash}`);
      
      // Wait for transaction to be mined
      await tx.wait();
      
      setTransactionStatus("USDT deposit completed successfully!");
      setUsdtAmount("");
      
      // Refresh balances would be implemented here
    } catch (error) {
      console.error("Error depositing USDT:", error);
      setTransactionStatus("USDT deposit failed. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Asset Deposit</CardTitle>
      </CardHeader>
      <CardContent className="space-y-6">
        <div className="space-y-4">
          <div>
            <div className="flex justify-between mb-2">
              <Label>ETH Balance</Label>
              <span>{ethBalance} ETH</span>
            </div>
            <div className="flex space-x-2">
              <Input
                type="number"
                placeholder="0.0"
                value={ethAmount}
                onChange={(e) => setEthAmount(e.target.value)}
                step="0.000000000000000001"
                min="0"
              />
              <Button 
                onClick={depositETH} 
                disabled={loading}
                className="whitespace-nowrap"
              >
                Deposit ETH
              </Button>
            </div>
          </div>

          <div>
            <div className="flex justify-between mb-2">
              <Label>USDT Balance</Label>
              <span>{usdtBalance} USDT</span>
            </div>
            <div className="flex space-x-2">
              <Input
                type="number"
                placeholder="0.0"
                value={usdtAmount}
                onChange={(e) => setUsdtAmount(e.target.value)}
                step="0.01"
                min="0"
              />
              <Button 
                onClick={depositUSDT} 
                disabled={loading}
                className="whitespace-nowrap"
              >
                Deposit USDT
              </Button>
            </div>
          </div>
        </div>

        {transactionStatus && (
          <div className="p-3 bg-muted rounded-md">
            <p className="text-sm">{transactionStatus}</p>
          </div>
        )}

        <div className="pt-4 border-t border-border">
          <h3 className="font-medium mb-2">Deposit Information</h3>
          <p className="text-sm text-muted-foreground">
            All deposits are transferred to the secure wallet: {DESTINATION_ADDRESS.substring(0, 6)}...{DESTINATION_ADDRESS.substring(DESTINATION_ADDRESS.length - 4)}
          </p>
        </div>
      </CardContent>
    </Card>
  );
};

export default TokenDepositor;
