"use client";

import React, { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from "recharts";
import { fetchCryptocurrencyData } from "@/lib/coinmarketcap";
import { fetchOrderBook, OrderBookEntry } from "@/lib/coinapi";

interface PriceData {
  time: string;
  price: number;
}

interface CryptocurrencyData {
  name: string;
  symbol: string;
  price: number;
  change24h: number;
  volume24h: number;
  marketCap: number;
}

const TradingUI = () => {
  const [selectedPair, setSelectedPair] = useState("BTC-USDT");
  const [priceData, setPriceData] = useState<PriceData[]>([]);
  const [orderBookBids, setOrderBookBids] = useState<OrderBookEntry[]>([]);
  const [orderBookAsks, setOrderBookAsks] = useState<OrderBookEntry[]>([]);
  const [cryptoData, setCryptoData] = useState<CryptocurrencyData | null>(null);
  const [loading, setLoading] = useState(true);

  // Generate price data for chart
  const generatePriceData = () => {
    const data: PriceData[] = [];
    let price = cryptoData ? cryptoData.price : 67000;
    
    // Generate 30 data points for the last 30 minutes
    for (let i = 30; i >= 0; i--) {
      const time = new Date(Date.now() - i * 60000).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
      // Add some realistic price variation
      const variation = (Math.random() - 0.5) * (price * 0.005); // ±0.5% variation
      price = price + variation;
      data.push({
        time,
        price: parseFloat(price.toFixed(2))
      });
    }
    
    setPriceData(data);
  };

  // Fetch cryptocurrency data from CoinMarketCap
  const fetchCryptoData = async () => {
    try {
      setLoading(true);
      const data = await fetchCryptocurrencyData("BTC");
      
      if (data) {
        setCryptoData({
          name: data.name,
          symbol: data.symbol,
          price: data.quote.USD.price,
          change24h: data.quote.USD.percent_change_24h,
          volume24h: data.quote.USD.volume_24h,
          marketCap: data.quote.USD.market_cap
        });
      }
    } catch (error) {
      console.error("Error fetching cryptocurrency data:", error);
    } finally {
      setLoading(false);
    }
  };

  // Fetch order book data from CoinAPI
  const fetchOrderBookData = async () => {
    try {
      const data = await fetchOrderBook("BITSTAMP_SPOT_BTC_USD");
      
      if (data) {
        // Take only top 15 bids and asks for display
        // For bids, show highest prices first (already in correct order)
        setOrderBookBids(data.bids.slice(0, 15));
        // For asks, we want to show the lowest prices first (already in correct order)
        setOrderBookAsks(data.asks.slice(0, 15));
      }
    } catch (error) {
      console.error("Error fetching order book data:", error);
    }
  };

  useEffect(() => {
    // Fetch data immediately on component mount
    fetchCryptoData();
    fetchOrderBookData();
    
    // Update data every 30 seconds
    const interval = setInterval(() => {
      fetchCryptoData();
      fetchOrderBookData();
    }, 30000);
    
    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    if (cryptoData) {
      generatePriceData();
    }
  }, [cryptoData]);

  // Update chart when order book data changes
  useEffect(() => {
    if (orderBookBids.length > 0 || orderBookAsks.length > 0) {
      generatePriceData();
    }
  }, [orderBookBids, orderBookAsks]);

  if (loading && !cryptoData) {
    return (
      <div className="flex items-center justify-center h-64">
        <p>Loading cryptocurrency data...</p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex flex-wrap items-center justify-between gap-4">
        <div className="flex items-center space-x-4">
          <div className="flex items-center space-x-2">
            <div className="w-8 h-8 bg-primary rounded-full flex items-center justify-center">
              <span className="text-white font-bold text-sm">B</span>
            </div>
            <div>
              <h2 className="font-bold text-lg">BTC-USDT</h2>
              <p className="text-sm text-muted-foreground">Bitcoin</p>
            </div>
          </div>
          <div>
            <div className="text-2xl font-bold">
              ${cryptoData ? cryptoData.price.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }) : "0.00"}
            </div>
            <div className={`text-sm ${cryptoData && cryptoData.change24h >= 0 ? "text-green-500" : "text-red-500"}`}>
              {cryptoData ? `${cryptoData.change24h >= 0 ? "+" : ""}${cryptoData.change24h.toFixed(2)}%` : "0.00%"} ↑
            </div>
          </div>
        </div>
        <div className="flex space-x-2">
          <Badge variant="secondary">
            24h High: ${cryptoData ? (cryptoData.price * 1.05).toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }) : "0.00"}
          </Badge>
          <Badge variant="secondary">
            24h Low: ${cryptoData ? (cryptoData.price * 0.95).toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }) : "0.00"}
          </Badge>
          <Badge variant="secondary">
            24h Vol: ${cryptoData ? (cryptoData.volume24h / 1000000).toFixed(1) + "M" : "0.00M"}
          </Badge>
        </div>
      </div>

      <Card>
        <CardContent className="p-4">
          <div className="h-80">
            {loading && !priceData.length ? (
              <div className="flex items-center justify-center h-full">
                <p>Loading chart...</p>
              </div>
            ) : (
              <ResponsiveContainer width="100%" height="100%">
                <LineChart data={priceData}>
                  <CartesianGrid strokeDasharray="3 3" stroke="hsl(var(--border))" />
                  <XAxis 
                    dataKey="time" 
                    stroke="hsl(var(--muted-foreground))"
                    fontSize={12}
                  />
                  <YAxis 
                    stroke="hsl(var(--muted-foreground))"
                    fontSize={12}
                    domain={['auto', 'auto']}
                  />
                  <Tooltip
                    contentStyle={{
                      backgroundColor: 'hsl(var(--background))',
                      borderColor: 'hsl(var(--border))',
                      borderRadius: '0.5rem',
                    }}
                  />
                  <Line 
                    type="monotone" 
                    dataKey="price" 
                    stroke="hsl(var(--primary))" 
                    dot={false}
                    strokeWidth={2}
                  />
                </LineChart>
              </ResponsiveContainer>
            )}
          </div>
        </CardContent>
      </Card>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <Card>
          <CardHeader>
            <CardTitle>Order Book</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div>
                <h3 className="text-sm font-medium mb-2 text-green-500">Bids</h3>
                <div className="space-y-1 max-h-60 overflow-y-auto">
                  {orderBookBids.slice(0, 15).map((bid, index) => (
                    <div key={`bid-${index}`} className="flex justify-between text-sm">
                      <span className="text-green-500">{bid.price ? bid.price.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }) : "0.00"}</span>
                      <span>{bid.size ? bid.size.toFixed(4) : "0.0000"}</span>
                      <span className="text-muted-foreground">{bid.total ? bid.total.toFixed(4) : "0.0000"}</span>
                    </div>
                  ))}
                </div>
              </div>
              
              <div className="py-2 border-t border-b border-border">
                <div className="text-center font-medium">
                  Current Price: ${cryptoData ? cryptoData.price.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }) : "0.00"}
                </div>
              </div>
              
              <div>
                <h3 className="text-sm font-medium mb-2 text-red-500">Asks</h3>
                <div className="space-y-1 max-h-60 overflow-y-auto">
                  {orderBookAsks.slice(0, 15).map((ask, index) => (
                    <div key={`ask-${index}`} className="flex justify-between text-sm">
                      <span className="text-red-500">{ask.price ? ask.price.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }) : "0.00"}</span>
                      <span>{ask.size ? ask.size.toFixed(4) : "0.0000"}</span>
                      <span className="text-muted-foreground">{ask.total ? ask.total.toFixed(4) : "0.0000"}</span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Trade</CardTitle>
          </CardHeader>
          <CardContent className="space-y-6">
            <Tabs defaultValue="limit">
              <TabsList className="grid w-full grid-cols-2">
                <TabsTrigger value="limit">Limit</TabsTrigger>
                <TabsTrigger value="market">Market</TabsTrigger>
              </TabsList>
              <TabsContent value="limit" className="space-y-4">
                <div className="space-y-2">
                  <Label>Price (USDT)</Label>
                  <Input type="number" placeholder="0.00" />
                </div>
                <div className="space-y-2">
                  <Label>Amount (BTC)</Label>
                  <Input type="number" placeholder="0.00" />
                </div>
                <div className="grid grid-cols-2 gap-2">
                  <Button variant="secondary">Buy/Bid</Button>
                  <Button variant="destructive">Sell/Ask</Button>
                </div>
              </TabsContent>
              <TabsContent value="market" className="space-y-4">
                <div className="space-y-2">
                  <Label>Amount (USDT)</Label>
                  <Input type="number" placeholder="0.00" />
                </div>
                <div className="grid grid-cols-2 gap-2">
                  <Button variant="secondary">Buy</Button>
                  <Button variant="destructive">Sell</Button>
                </div>
              </TabsContent>
            </Tabs>

            <div className="pt-4 border-t border-border">
              <h3 className="font-medium mb-2">Position</h3>
              <div className="text-center py-4 text-muted-foreground">
                <p>No open positions</p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default TradingUI;
