export interface Cryptocurrency {
  id: number;
  name: string;
  symbol: string;
  quote: {
    USD: {
      price: number;
      volume_24h: number;
      percent_change_1h: number;
      percent_change_24h: number;
      percent_change_7d: number;
      market_cap: number;
    }
  };
}

export const fetchCryptocurrencyData = async (symbol: string): Promise<Cryptocurrency | null> => {
  try {
    const response = await fetch(`/api/coinmarketcap?symbol=${symbol}`);
    
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const data = await response.json();
    return data;
  } catch (error) {
    console.error("Error fetching cryptocurrency data:", error);
    return null;
  }
};
