export interface OrderBookEntry {
  price: number;
  size: number;
  total?: number;
}

export interface OrderBookData {
  symbol_id: string;
  time_exchange: string;
  time_coinapi: string;
  asks: OrderBookEntry[];
  bids: OrderBookEntry[];
}

export const fetchOrderBook = async (symbol: string = "BITSTAMP_SPOT_BTC_USD"): Promise<OrderBookData | null> => {
  // Always return mock data for order book due to API quota issues
  console.warn("Using mock order book data due to API quota issues");
  return generateMockOrderBook();
};

// Generate mock order book data for testing
const generateMockOrderBook = (): OrderBookData => {
  const currentPrice = 70000; // Mock current price
  const bids: OrderBookEntry[] = [];
  const asks: OrderBookEntry[] = [];
  
  let bidTotal = 0;
  let askTotal = 0;
  
  // Generate bids (buy orders)
  for (let i = 0; i < 20; i++) {
    const price = currentPrice - (i * 50);
    const size = Math.random() * 5 + 0.1;
    bidTotal += size;
    bids.push({
      price,
      size,
      total: bidTotal
    });
  }
  
  // Generate asks (sell orders)
  for (let i = 0; i < 20; i++) {
    const price = currentPrice + (i * 50);
    const size = Math.random() * 5 + 0.1;
    askTotal += size;
    asks.push({
      price,
      size,
      total: askTotal
    });
  }
  
  return {
    symbol_id: "BTC-USDT",
    time_exchange: new Date().toISOString(),
    time_coinapi: new Date().toISOString(),
    bids,
    asks
  };
};
