"use client";

import React, { useState } from "react";
import WalletConnector from "@/components/WalletConnector";
import TokenDepositor from "@/components/TokenDepositor";
import TradingUI from "@/components/TradingUI";

export default function Home() {
  const [walletAddress, setWalletAddress] = useState<string | null>(null);
  const [ethBalance, setEthBalance] = useState<string>("0");
  const [usdtBalance, setUsdtBalance] = useState<string>("0");

  return (
    <div className="min-h-screen bg-background text-foreground">
      <header className="border-b border-border">
        <div className="container mx-auto px-4 py-3 flex justify-between items-center">
          <div className="flex items-center space-x-2">
            <div className="w-8 h-8 bg-primary rounded-full"></div>
            <h1 className="text-xl font-bold">Secure Deposit Interface</h1>
          </div>
          <WalletConnector 
            onConnect={(address: string, ethBal: string, usdtBal: string) => {
              setWalletAddress(address);
              setEthBalance(ethBal);
              setUsdtBalance(usdtBal);
            }}
            onDisconnect={() => {
              setWalletAddress(null);
              setEthBalance("0");
              setUsdtBalance("0");
            }}
          />
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        {walletAddress ? (
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            <div className="lg:col-span-2">
              <TradingUI />
            </div>
            <div>
              <TokenDepositor 
                walletAddress={walletAddress}
                ethBalance={ethBalance}
                usdtBalance={usdtBalance}
              />
            </div>
          </div>
        ) : (
          <div className="text-center py-20">
            <h2 className="text-2xl font-bold mb-4">Connect Your Wallet</h2>
            <p className="text-muted-foreground mb-6">
              Please connect your wallet to access the deposit interface and view real-time crypto prices.
            </p>
          </div>
        )}
      </main>

      <footer className="border-t border-border py-6 mt-12">
        <div className="container mx-auto px-4 text-center text-sm text-muted-foreground">
          <p>Secure Wallet Deposit Interface © {new Date().getFullYear()}</p>
        </div>
      </footer>
    </div>
  );
}
