"use client";

import React, { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { ethers } from "ethers";

interface WalletConnectorProps {
  onConnect: (address: string, ethBalance: string, usdtBalance: string) => void;
  onDisconnect: () => void;
}

const WalletConnector: React.FC<WalletConnectorProps> = ({ onConnect, onDisconnect }) => {
  const [isConnected, setIsConnected] = useState(false);
  const [walletAddress, setWalletAddress] = useState("");
  const [loading, setLoading] = useState(false);

  // USDT contract address on Ethereum mainnet
  const USDT_CONTRACT_ADDRESS = "0xdAC17F958D2ee523a2206206994597C13D831ec7";

  // USDT contract ABI (simplified for balance checking)
  const USDT_ABI = [
    "function balanceOf(address owner) view returns (uint256)"
  ];

  const connectWallet = async () => {
    try {
      setLoading(true);
      
      if (typeof window.ethereum !== 'undefined') {
        // Request account access
        const accounts = await window.ethereum.request({ 
          method: 'eth_requestAccounts' 
        });
        
        const provider = new ethers.BrowserProvider(window.ethereum);
        const signer = await provider.getSigner();
        const address = await signer.getAddress();
        
        // Get ETH balance
        const ethBalance = await provider.getBalance(address);
        const ethBalanceFormatted = ethers.formatEther(ethBalance);
        
        // Get USDT balance
        const usdtContract = new ethers.Contract(USDT_CONTRACT_ADDRESS, USDT_ABI, provider);
        const usdtBalance = await usdtContract.balanceOf(address);
        const usdtBalanceFormatted = ethers.formatUnits(usdtBalance, 6); // USDT has 6 decimals
        
        setWalletAddress(address);
        setIsConnected(true);
        onConnect(address, ethBalanceFormatted, usdtBalanceFormatted);
      } else {
        alert("Please install MetaMask or another Ethereum wallet extension.");
      }
    } catch (error) {
      console.error("Error connecting wallet:", error);
      alert("Failed to connect wallet. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  const disconnectWallet = () => {
    setIsConnected(false);
    setWalletAddress("");
    onDisconnect();
  };

  return (
    <div>
      {isConnected ? (
        <div className="flex items-center space-x-2">
          <div className="bg-muted px-3 py-1 rounded-full text-sm">
            <span className="hidden sm:inline">Connected: </span>
            {walletAddress.substring(0, 6)}...{walletAddress.substring(walletAddress.length - 4)}
          </div>
          <Button variant="outline" onClick={disconnectWallet}>
            Disconnect
          </Button>
        </div>
      ) : (
        <Button onClick={connectWallet} disabled={loading}>
          {loading ? "Connecting..." : "Connect Wallet"}
        </Button>
      )}
    </div>
  );
};

export default WalletConnector;
